@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600&display=swap&subset=latin-ext);
.team-members-container {
  display: flex;
  flex-wrap: wrap;
  max-width: 1300px;
  justify-content: space-around;
  margin: 0 auto; }

.team-member {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 60px 30px 0 30px;
  width: 180px; }
  .team-member .member-avatar {
    overflow: hidden;
    display: inline-block;
    position: relative; }
    .team-member .member-avatar .member-img-over {
      background: rgba(66, 195, 222, 0.8);
      display: block;
      overflow: hidden;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      z-index: 1;
      -webkit-transition: 1s;
      transition: 1s;
      cursor: pointer; }
      .team-member .member-avatar .member-img-over:hover {
        background: rgba(66, 195, 222, 0); }
      .team-member .member-avatar .member-img-over:hover + .member-img {
        -webkit-filter: none;
                filter: none; }
    .team-member .member-avatar .member-img {
      display: block;
      -webkit-filter: grayscale(1) contrast(190%);
              filter: grayscale(1) contrast(190%);
      -webkit-transition: 1s;
      transition: 1s; }
    .team-member .member-avatar .member-mail {
      width: 40px;
      height: 40px;
      background: #452E76;
      border-radius: 50%;
      display: flex;
      position: absolute;
      right: 10px;
      top: 10px;
      color: #fff;
      justify-content: center;
      align-items: center;
      z-index: 2;
      -webkit-transition: .4s;
      transition: .4s;
      -webkit-transform: translateZ(0);
      transform: translateZ(0); }
      .team-member .member-avatar .member-mail img {
        -webkit-transform: translateZ(0);
        transform: translateZ(0); }
  .team-member .member-name {
    font-size: 20px;
    font-weight: 400;
    color: #452E76;
    margin: 20px 0;
    text-align: center; }
  .team-member .member-position {
    font-size: 14px;
    font-weight: 300;
    color: #42C3DE;
    margin-bottom: 3px;
    text-align: center; }
  .team-member .member-location {
    font-size: 14px;
    font-weight: 300;
    text-align: center;
    color: #808080; }

.slider-container {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  min-width: 100%;
  max-width: 100%;
  min-height: 100%;
  max-height: 100%;
  z-index: 10;
  background-color: #00000099; }

.slider-center {
  width: 100%;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%); }

.slider {
  position: relative;
  cursor: default;
  width: 90%;
  margin: 0 auto; }
  .slider button {
    background-color: transparent;
    border: none;
    color: white;
    position: absolute;
    outline: none; }
  .slider .prev-btn {
    top: 48%;
    left: 2%; }
    .slider .prev-btn .prev-arrow {
      font-size: 5vw;
      cursor: pointer; }
  .slider .next-btn {
    top: 48%;
    right: 2%; }
    .slider .next-btn .next-arrow {
      font-size: 5vw;
      cursor: pointer; }
  .slider .close-btn {
    top: 1%;
    right: 2%; }
    .slider .close-btn .close {
      font-size: 6vw;
      cursor: pointer; }

.slide {
  position: relative;
  width: 100%;
  height: 100%; }
  .slide .slide-background {
    overflow: hidden;
    display: inline-block;
    position: relative;
    min-width: 100%;
    max-width: 100%;
    min-height: 100%;
    max-height: 100%; }
    .slide .slide-background .slide-over {
      background: rgba(66, 195, 222, 0.5);
      display: block;
      overflow: hidden;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 1; }
    .slide .slide-background .slide-img {
      display: block;
      max-width: 100%;
      max-height: 100%;
      object-fit: contain; }
  .slide .slide-content-border {
    border: 5px dashed white;
    min-width: 60%;
    min-height: 40%;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  .slide .slide-text {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 60%;
    min-height: 40%;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 10;
    text-shadow: 1px 1px 5px  #444; }
    .slide .slide-text .slide-member-position {
      color: white;
      font-size: 2.5vw;
      text-align: center;
      font-weight: 300; }
    .slide .slide-text .slide-member-name {
      color: white;
      font-size: 5vw;
      font-weight: 600; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif; }

body:after {
  content: "";
  background-image: url(/IDH-task/static/media/bg.86377fca.png);
  background-repeat: repeat;
  opacity: 0.3;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1; }

#root {
  position: relative; }

.page-title {
  color: #452E76;
  font-weight: 600;
  font-size: 60px;
  text-align: center;
  margin-bottom: 40px;
  padding-top: 40px; }

